import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCalendarCheck, faAngleUp } from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';
import './Hero.css';
import { isMobile } from 'react-device-detect';

function Hero() {
	const navigate = useNavigate();
	const [goUp, setGoUp] = useState(false);

	const scrollToTop = () => {
		window.scrollTo({ top: 0, behavior: 'smooth' });
	};

	const handleBookAppointmentClick = () => {
		navigate('/appointment');
	};

	useEffect(() => {
		const onPageScroll = () => {
			if (window.scrollY > 600) {
				setGoUp(true);
			} else {
				setGoUp(false);
			}
		};
		window.addEventListener('scroll', onPageScroll);

		return () => {
			window.removeEventListener('scroll', onPageScroll);
		};
	}, []);

	return (
		<div className="section-container">
			<div className="hero-section">
				<div className="hero-image-section" />

				<div className="text-section" style={{padding: '200px 50px 100px 0'}}>
					<p className="text-headline">{`Work with us today`}</p>
					<h2 className="text-title">{`Impact through simple solutions`}</h2>
					<p className="text-descritpion">{`Let's talk. We listen and understand. Then allow us to provide you with our technical expertise and solutions.
					No application is too complex as long as requirements are clear and collaborations are plenty.`}</p>
					{/* {!isMobile && <p className="text-descritpion">{`We transform the digital ambitions of our partners into reality. We work closely with our customers throughout development to ensure that we are still aligned with the end-goal. We are committed to producing exceptional software for each of our partners.`}</p>} */}

					<button className="text-appointment-btn" type="button" onClick={handleBookAppointmentClick} style={{margin: '80px'}}>
						<FontAwesomeIcon icon={faCalendarCheck} /> {`Contact Us To Find How`}
					</button>

					

					
				</div>

			</div>

			<div onClick={scrollToTop} className={`scroll-up ${goUp ? 'show-scroll' : ''}`}>
				<FontAwesomeIcon icon={faAngleUp} />
			</div>
			{/* <img className="hero-image3" src={Office} alt="Office" /> */}
		</div>
	);
}

export default Hero;
