import React from 'react';
import './Flowhero/Info.css';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

function InformationCard(props) {
	return (
		<div className="info-cards">
			{props.icon && (
				<span className="info-card-icon">
					<FontAwesomeIcon className="info-fa-icon" icon={props.icon} />
				</span>
			)}
			<p className="info-card-title" style={{ textAlign: 'center' }}>{props.title}</p>
			{props?.description && <p className="info-card-description" style={{ textAlign: 'center' }}>{props.description}</p>}
			{props?.moreinfo && <p className="info-card-moreinfo" style={{ textAlign: 'center' }}>{props?.moreinfo}</p>}
		</div>
	);
}

export default InformationCard;

export const InformationCardv2 = props => {
	return (
		<div className="info-cards" >
			{props.icon && (
				<div className="info-card-icon">
					<FontAwesomeIcon className="info-fa-icon" icon={props.icon} />
				</div>
			)}
			<p className="info-card-title" >{props.title}</p>
			{props?.description && <div className="info-card-description" style={{ margin: '0' }}>{props.description}</div>}
			{props?.moreinfo && <p className="info-card-moreinfo">{props?.moreinfo}</p>}
		</div>
	);
} 
