import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faRectangleXmark } from '@fortawesome/free-solid-svg-icons';
import cls from './Navbar.module.scss';
import { Link } from 'react-router-dom';
import { isMobile } from 'react-device-detect';
import { FLOWHEROAPPS } from '../../../shared/utility';

function Navbar(props) {
	const { background = 'transparent' } = props;
	const [nav, setNav] = useState(false);

	const openNav = () => {
		setNav(!nav);
	};

	return (
		<div className={cls.navbarsection} style={{ position: 'fixed', height: '100px', backgroundColor: background }}>

			<ADPLOGO />
			{/* Desktop */}
			<ul className={cls.navbaritems} style={{ display: isMobile ? 'none' : 'flex' }}>
				<li>
					<a onClick={() => {

						window.scrollTo({ top: 0, behavior: 'smooth' });
					}} className={cls.navbarlinks} style={{ cursor: 'pointer' }}>
						{'HOME'}
					</a>
				</li>
				<li>
					<a onClick={() => window.scrollTo({ top: document.body.scrollHeight / 3, behavior: 'smooth' })} className={cls.navbarlinks} style={{ cursor: 'pointer' }}>
						{'PRODUCTS'}
					</a>
				</li>
				<li>
					<a onClick={() => window.scrollTo({ top: document.body.scrollHeight / 2, behavior: 'smooth' })} className={cls.navbarlinks} style={{ cursor: 'pointer' }}>
						{'OUR STACK'}
					</a>
				</li>
				<li>
					<a href="#/flowhero" className={cls.navbarlinks}>
						{'FLOWHERO™'}
					</a>
				</li>
				<li>
					<a onClick={() => window.scrollTo({ top: document.body.scrollHeight, behavior: 'smooth' })} className={cls.navbarlinks} style={{ cursor: 'pointer' }}>
						{'ABOUT US'}
					</a>
				</li>
			</ul>

			{/* Mobile */}
			<div className={`mobile-navbar ${nav ? 'open-nav' : ''}`} style={{height: '40%'}}>
				<div onClick={openNav} className="mobile-navbar-close">
					<FontAwesomeIcon icon={faRectangleXmark} className="hamb-icon" style={{ color: 'var(--app-color-gray)', width: '25px', height: '25px' }} />
				</div>

				<ul className={cls.mobilenavbarlinks}>
					<li>
						<Link onClick={openNav} to="/">
							{'HOME'}
						</Link>
					</li>

					<li>
						<a onClick={openNav} href="/#/flowhero">
							{'FLOWHERO™'}
						</a>
					</li>
				</ul>
			</div>

			{/* Hamburger Icon */}
			<div className="mobile-nav" style={{ display: isMobile ? 'block' : 'none' }}>
				<FontAwesomeIcon icon={faBars} onClick={openNav} className="hamb-icon" />
			</div>
		</div>
	);
}

export default Navbar;

export const ADPLOGO = () => {
	return <div style={{ display: 'flex', justifyItems: 'center', alignItems: 'center', alignContent: 'center' }}>
		{!isMobile && <div className={cls.containerNav}>
			<div className={cls.circleNav}></div>
			<div className={cls.smallcircleNav}></div>
			<div className={cls.smallercircleNav}></div>
			<div className={cls.smallestcircleNav}></div>
		</div>}

		<div className={cls.navbartitle} style={{ marginBottom: '0px', marginLeft: isMobile ? '0' : '50px' }}>
			{FLOWHEROAPPS(isMobile ? '20px' : '24px', 'var(--app-color-gray-dark)', 'bold')}
		</div>
	</div>;
}


